import { prisma } from "@/lib/prisma"; // Adjust this path to where your Prisma client is

export async function DELETE(req) {
  try {
    const body = await req.json();
    const { id, userId } = body;

    if (!id || !userId) {
      return new Response(
        JSON.stringify({ error: "Missing goal ID or user ID" }),
        { status: 400 }
      );
    }

    // Check if the goal exists and belongs to this user
    const goal = await prisma.goal.findUnique({
      where: { id: Number(id) },
    });

    if (!goal || goal.userId !== userId) {
      return new Response(
        JSON.stringify({ error: "Goal not found or unauthorized" }),
        { status: 404 }
      );
    }

    // Delete the goal (related actions will be deleted if Prisma cascade is set)
    await prisma.goal.delete({
      where: { id: Number(id) },
    });

    return new Response(JSON.stringify({ success: true }), { status: 200 });
  } catch (err) {
    console.error("DELETE error:", err);
    return new Response(JSON.stringify({ error: err.message }), { status: 500 });
  }
}
